.screen-reader-text {
	margin: 0px;
	overflow: hidden;
	width: 1px;
	position: absolute !important;
	word-wrap: normal !important;
	color: var(--am_acc_btn_color);
	background-color: var(--am_acc_btn_bgcolor);
	clip: auto !important;
	border: unset !important;
	border-radius: 0;
	left: -100%;
	font-size: var(--am_font_size_14);

	clip-path: none;
	font-weight: 700;
	height: auto;
	line-height: normal;
	padding: 8px 12px;
	text-decoration: none;
	top: 8px;

	&:hover,
	&:active,
	&:focus {
		display: block;
		width: auto;
		z-index: 100000;
		left: 8px;

	}
}

#page-section[tabindex="-1"]:focus {
	outline: 0;
}


